$light: rgb(0, 183, 255);
$middle: rgb(0, 0, 255);
$black: rgb(6, 0, 41);
$black-opacity: rgba(6, 0, 41, 0.6);
$black-opacity_l: rgba(6, 0, 41, 0.2);

.header {
  width: 100%;
  height: 100vh;

  background: lightblue url(../img/header_fon.jpg) no-repeat center;
  background-size: cover;

  font-family: 'Alice', serif;
  .menu {
    height: 70px;
    background-image: linear-gradient($black-opacity, $black-opacity_l);
    position: fixed;
    width: 100%;
    z-index: 1;

    a:visited,
    a:link,
    a{
      color: white;
      text-decoration: none;
      transition: 0.3s;
    }

    .menu_list {
      width: 100%;
      max-width: 1440px;
      margin: auto;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding: 0px 20px;

      .btn_phone_menu{
        display: none;
        transition: 0.4s;
        // display: inline-block;
        cursor: pointer;
        position: fixed;
        transform: translate(-50%, -50%);
        top: 35px;
        left: 35px;

        .bar1,
        .bar2,
        .bar3 {
          width: 28px;
          height: 3px;
          background-color: #bababa;
          margin: 5px 0;
        }
      }
      .btn_phone_menu_active {
        z-index: 101;
        position: fixed;
  
        .bar1 {
          transform: rotate(-45deg) translate(-4px, 4px);
        }

        .bar2 {
          opacity: 0;
        }

        .bar3 {
          transform: rotate(45deg) translate(-6px, -7px);
        }
      }

      #logo {

        img{
          width: 70px;
        }
      }

      .item_desktop_menu{
        display: flex;
        flex-direction: row;
        text-transform: uppercase;

        li{
          margin: 0px 20px;
        }
        li a:hover{
          color: rgb(137, 231, 255);
        }
      }

      .menu_social_item {
        display: flex;
        flex-direction: row;

        li {
          margin: 0px 2px;
          height: 30px;
          font-size: 22px;
          width: 30px;
          background-color: #fff;
          border-radius: 50%;
          text-align: center;
          line-height: 32px;

          a:visited,
          a:link,
          a {
            color: black;
          }
        }
        li:hover{
          background-color: $black-opacity;
        }
        li:hover a{
          color: rgb(137, 231, 255);
        }
         
      }
    }
  }
  .header_content {
    height: 100%;
    padding: 70px 10px;
  
    text-align: center;
  }
}

.phone_menu {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  padding: 0px 0px 0px 40px;
  width: 260px;
  position: fixed;
  left: -300px;
  top: 50px;
  height: 95vh;
  color: #fff;
  background-color: #000;
  z-index: 0;
  transition-timing-function: linear;
  transition: 0.6s;
}

.phone_menu_active {
  display: flex;
  transform: translate(300px, 0px);
  box-shadow: 0 0px 1000px 1000px rgba(255, 255, 255, 0.9);
  z-index: 1;
}

.phone_list {
  font-size: 21px;
  line-height: 1.9;
  padding: 40px 0px;
}
.phone_social_list {
  display: flex;
  flex-direction: row;
  padding-bottom: 60px;
}

.phone_social_list li {
  margin: 0px 5px;
}

.phone_list_item a:visited {
  color: white;
}
.phone_list_item a:link {
  color: white;
}
